.main-nav__list {
  display: flex;
  flex-direction: column;
  padding: 20px 26px 20px 41px;
  position: absolute;
  content: "";
  top: 82px;
  right: 0;
  z-index: 2;
  width: 231px;
  background-color: #181818;
}

@media (min-width: @vertical-tablet-width) {
  .main-nav__list {
    top: 80px;
  }
}

@media (min-width: @desktop-width) {
  .main-nav__list {
    flex-direction: row;
    position: static;
    align-items: center;
    width: auto;
    padding: 0;
  }
}

.main-nav__item:not(:last-child) {
  margin-bottom: 30px;
}

@media (min-width: @desktop-width) {
  .main-nav__item:not(:last-child) {
    margin-bottom: 0;
    margin-right: 90px;
  }
}

.main-nav__link {
  color: #f8f8f8;

  &:hover,
  &:focus {
    border-bottom: 2px solid #f8f8f8;
  }
}

.main-nav__toggle {
  width: 32px;
  height: 26px;
  display: block;
  background-color: transparent;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 20px;
  cursor: pointer;
  border: none;
  padding: 0;

  &:focus {
    outline: none;
  }

  &:focus .main-nav__burger-line,
  &:focus .main-nav__burger-line::before,
  &:focus .main-nav__burger-line::after {
    background-color: #f8f8f8;
  }
}

.main-nav--closed .main-nav__burger-line,
.main-nav--closed .main-nav__burger-line::after,
.main-nav--closed .main-nav__burger-line::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #f8f8f8;
  display: block;
  transition: 0.3s;
  top: 11px;
  left: 0;
}

.main-nav--closed .main-nav__burger-line::before {
  top: -11px;
  transition: top 350ms 350ms, transform 350ms cubic-bezier(0.23, 1, 0.32, 1);
}

.main-nav--closed .main-nav__burger-line::after {
  bottom: -11px;
  transition: top 350ms 350ms, transform 350ms cubic-bezier(0.23, 1, 0.32, 1);
}

.main-nav--closed .main-nav__burger-line:hover,
.main-nav--closed .main-nav__burger-line:hover::before,
.main-nav--closed .main-nav__burger-line:hover::after {
  background-color: #f8f8f8;
}

.main-nav--opened .main-nav__toggle .main-nav__burger-line::before,
.main-nav--opened .main-nav__toggle .main-nav__burger-line::after,
.main-nav--opened .main-nav__burger-line {
  position: absolute;
  width: 100%;
  height: 2px;
  content: "";
  background-color: #f8f8f8;
  top: 9px;
  left: 0;
  display: block;
}

.main-nav--opened .main-nav__toggle .main-nav__burger-line::before {
  top: 0;
  transform: rotate(45deg);
  transition: top 350ms, transform 350ms 350ms cubic-bezier(0.68, -0.55, 0, 1.55);
}

.main-nav--opened .main-nav__toggle .main-nav__burger-line::after {
  top: 0;
  transform: rotate(-45deg);
  transition: top 350ms, transform 350ms 350ms cubic-bezier(0.68, -0.55, 0, 1.55);
}

.main-nav--opened .main-nav__toggle .main-nav__burger-line {
  background-color: rgba(15, 41, 61, 0);
}

.main-nav--opened .main-nav__burger-line:hover::before,
.main-nav--opened .main-nav__burger-line:hover::after {
  background-color: #f8f8f8;
}

@media (min-width: @vertical-tablet-width) {
  .main-nav__toggle {
    right: 20px;
  }
}

@media (min-width: @tablet-width) {
  .main-nav__toggle {
    right: 42px;
  }
}

@media (min-width: @desktop-width) {
  .main-nav__toggle {
    display: none;
  }
}
