.sterilisation {
  background-color: #181818;

  .wrapper {
    color: #f8f8f8;
    padding: 40px 20px 50px 30px;
  }
}

@media (min-width: @big-mobile-width) {
  .sterilisation {
    .wrapper {
      padding: 40px 80px 50px;
    }
  }
}

@media (min-width: @vertical-tablet-width) {
  .sterilisation {
    .wrapper {
      padding: 50px 20px 70px;
    }
  }
}

@media (min-width: @tablet-width) {
  .sterilisation {
    .wrapper {
      padding: 60px 42px 58px;
    }
  }
}

@media (min-width: @desktop-width) {
  .sterilisation {
    .wrapper {
      padding: 60px 20px 90px;
      max-width: 1200px;
      margin: 0 auto;
    }
  }
}

.sterilisation__title {
  margin: 0 57px 28px 0;
  text-align: center;
}

@media (min-width: @vertical-tablet-width) {
  .sterilisation__title {
    margin: 0 0 60px 0;
    text-align: left;
  }
}

@media (min-width: @tablet-width) {
  .sterilisation__title {
    margin-bottom: 56px;
  }
}

.sterilisation__title--zone {
  font-size: 14px;
  text-transform: lowercase;
  display: block;
  margin-top: -12px;
  margin-left: 214px;
}

@media (min-width: @vertical-tablet-width) {
  .sterilisation__title--zone {
    font-size: 20px;
    margin-top: -28px;
    margin-left: 289px;
  }
}

@media (min-width: @tablet-width) {
  .sterilisation__title--zone {
    font-size: 20px;
    margin-top: -31px;
    margin-left: 291px;
  }
}

@media (min-width: @desktop-width) {
  .sterilisation__title--zone {
    margin-top: -30px;
    margin-left: 370px;
  }
}

.sterilisation__list {
  border-left: 1px solid #f8f8f8;
}

@media (min-width: @vertical-tablet-width) {
  .sterilisation__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    row-gap: 30px;
    border-left: none;
  }
}

@media (min-width: @tablet-width) {
  .sterilisation__list {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: none;
    row-gap: 0;
    column-gap: 15px;
  }
}

@media (min-width: @desktop-width) {
  .sterilisation__list {
    column-gap: 108px;
  }
}

.sterilisation__list-item {
  display: flex;
  gap: 20px;
  padding-left: 20px;
}

@media (min-width: @vertical-tablet-width) {
  .sterilisation__list-item {
    flex-direction: column;
    gap: 25px;
    padding-left: 15px;
    border-left: 1px solid #f8f8f8;
    width: 209px;
  }
}

.sterilisation__list-item:not(:last-child) {
  margin-bottom: 38px;
}

@media (min-width: @vertical-tablet-width) {
  .sterilisation__list-item:not(:last-child) {
    margin-bottom: 0;
  }

  .sterilisation__list-item:nth-child(3) {
    margin-left: 38%;
  }

  .sterilisation__list-item:nth-child(4) {
    margin-left: 33%;
  }
}

@media (min-width: @tablet-width) {
  .sterilisation__list-item:nth-child(3),
  .sterilisation__list-item:nth-child(4) {
    margin-left: 0;
  }
}

@media (min-width: @desktop-width) {
  .sterilisation__list-item {
    padding-left: 20px;
  }

  .sterilisation__list-item:first-of-type {
    width: 194px;
  }

  .sterilisation__list-item:nth-of-type(2) {
    width: 208px;
  }

  .sterilisation__list-item:nth-of-type(3) {
    width: 181px;
  }

  .sterilisation__list-item:last-of-type {
    width: 251px;
  }
}

.sterilisation__desc {
  font-family: "BonaNova-Regular", "Arial", sans-serif;
  font-weight: 400;
  margin: 0;
}

@media (min-width: @vertical-tablet-width) {
  .sterilisation__desc {
    font-size: 14px;
  }
}

@media (min-width: @desktop-width) {
  .sterilisation__desc {
    font-size: 18px;
  }
}

@media (min-width: @vertical-tablet-width) {
  .sterilisation__desc:last-child {
    margin-bottom: 20px;
  }
}

@media (min-width: @desktop-width) {
  .sterilisation__desc:last-child {
    margin-bottom: 29px;
  }
}

.sterilisation__step {
  text-transform: uppercase;
  font-size: 16px;
  min-width: 55px;
  margin: 0;
}

@media (min-width: @vertical-tablet-width) {
  .sterilisation__step {
    font-size: 24px;
  }
}

@media (min-width: @desktop-width) {
  .sterilisation__step {
    font-size: 30px;
  }
}
