.big-picture {
  .wrapper {
    max-width: 1200px;
    position:relative;
    margin: 0 auto;
  }
}

.big-picture__preview {
  position: absolute;
  top: 150px;
  left: 50%;

  min-width: 80%;

  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}

@media (min-width: @vertical-tablet-width) {
  .big-picture__preview {
    top: 60px;
  }
}

.big-picture__img img {
  display: block;
}

.social {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;

  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
     -ms-flex-direction: column;
         flex-direction: column;

  max-width: 600px;

  color: #000000;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  text-transform: none;

  background-color: #ffffff;
}

.big-picture__cancel {
  position: absolute;
  top: 0;
  left: 100%;

  margin-left: 10px;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;

  -webkit-box-sizing: border-box;
          box-sizing: border-box;

  width: 100%;
  height: 100%;
  padding-top: 60px;
  overflow: auto;

  background-color: rgba(0, 0, 0, 0.8);
}

.cancel {
  display: block;
  margin: 0;
  padding: 0;

  width: 22px;
  height: 22px;

  font-size: 0;

  background-color: rgba(255, 255, 255, 0.2);
  background-image: url("../img/icon-cross.svg");
  background-position: center;
  background-repeat: no-repeat;

  border: 0;
  border-radius: 2px;

  opacity: 0.6;
}

@media (min-width: @vertical-tablet-width) {
  .cancel {
    width: 42px;
    height: 42px;

    background-size: 22px 22px;
  }
}

.cancel:hover,
.cancel:focus {
  background-color: rgba(255, 255, 255, 0.4);
  opacity: 1;
}

.cancel:active {
  -webkit-transform: translateY(1px);
      -ms-transform: translateY(1px);
          transform: translateY(1px);
}

.big-picture__slider-button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  position: absolute;
  content: "";
  top: 33%;
  z-index: 1;

  color: #bababa;

  &:hover,
  &:focus {
    outline: none;
    color: #3f3f3f;
  }

  &:disabled {
    color: #181818;
  }
}

.big-picture__slider-button--next {
  right: -13%;
}

.big-picture__slider-button--prev {
  transform: rotate(180deg) translateY(8%);
  left: -13%;
}

@media (min-width: @big-mobile-width) {
  .big-picture__slider-button {
    top: 39%;
  }

  .big-picture__slider-button--prev {
    left: -10%;
  }

  .big-picture__slider-button--next {
    right: -10%;
  }
}

@media (min-width: @vertical-tablet-width) {
  .big-picture__slider-button {
    top: 41%;
  }
}

@media (min-width: @vertical-tablet-width) {
  .big-picture__slider-icon {
    width: 27px;
    height: 50px;
  }
}
