.reasons {
  background-color: #f8f8f8;
  position: relative;

  .wrapper {
    padding: 38px 20px;
  }
}

@media (min-width: @big-mobile-width) {
  .reasons {
    .wrapper {
      padding: 40px 80px;
    }
  }
}

@media (min-width: @vertical-tablet-width) {
  .reasons {
    .wrapper {
      padding: 60px 20px 70px;
    }
  }
}

@media (min-width: @tablet-width) {
  .reasons {
    .wrapper {
      padding: 60px 42px 90px;
    }
  }
}

@media (min-width: @desktop-width) {
  .reasons {
    .wrapper {
      padding: 48px 30px 78px 20px;
      max-width: 1200px;
      margin: 0 auto;
    }
  }
}

.reasons--waxing {
  background-color: #181818;
  color: #f8f8f8;
}

.reasons__title {
  text-align: center;
  margin: 0;
  margin-bottom: 30px;
}

@media (min-width: @vertical-tablet-width) {
  .reasons__title {
    text-align: left;
    width: 450px;
    margin-bottom: 50px;
  }
}

@media (min-width: @tablet-width) {
  .reasons__title {
    margin-bottom: 22px;
  }
}

@media (min-width: @desktop-width) {
  .reasons__title {
    margin-bottom: 0;
    width: 600px;
  }
}

.reasons__item {
  font-family: "BonaNova-Regular", "Arial", sans-serif;
  font-weight: 400;
}

.reasons__item:not(:last-child) {
  margin-bottom: 20px;
}

.reasons__item:last-child {
  margin-bottom: 30px;
}

@media (min-width: @vertical-tablet-width) {
  .reasons__item:not(:last-child) {
    margin-bottom: 34px;
  }

  .reasons__item:last-child {
    margin-bottom: 0;
  }
}

@media (min-width: @desktop-width) {
  .reasons__item:not(:last-child) {
    margin-bottom: 60px;
  }
}

.reasons__text--bold {
  font-family: "BonaNova-Bold", "Arial", sans-serif;
  font-weight: 700;
}

.reasons__box {
  display: flex;
  align-items: center;
  margin: 0;
  min-width: 320px;
}

@media (max-width: @small-mobile-width-only) {
  .reasons__box {
    min-width: 280px;
  }
}

@media (min-width: @vertical-tablet-width) {
  .reasons__box {
    min-width: 240px;
    width: 240px;
  }
}

@media (min-width: @tablet-width) {
  .reasons__box {
    min-width: 320px;
  }
}

@media (min-width: @desktop-width) {
  .reasons__box {
    min-width: 350px;
    margin-top: -8px;
  }
}

.reasons__img {
  display: inline-block;
  border-radius: 11px;
  width: 100%;
}

@media (min-width: @vertical-tablet-width) {
  .reasons__container {
    display: flex;
    gap: 60px;
  }
}

@media (min-width: @tablet-width) {
  .reasons__container {
    gap: 80px;
  }
}

@media (min-width: @tablet-width) {
  .reasons__list {
    margin-left: 210px;
    margin-top: 83px;
  }
}

@media (min-width: @desktop-width) {
  .reasons__list {
    margin-left: 274px;
    margin-top: 69px;
  }
}

.reasons__triangle {
  width: 50%;
  height: 0;
  padding-top: 14px;
  clip-path: polygon(100% 0%, 0% 100%, 100% 100%);
  background-color: #181818;
  position: absolute;
  bottom: 0;
  right: 0;
}

@media (min-width: @vertical-tablet-width) {
  .reasons__triangle {
    padding-top: 22px;
  }
}

@media (min-width: @tablet-width) {
  .reasons__triangle {
    padding-top: 32px;
  }
}

@media (min-width: @desktop-width) {
  .reasons__triangle {
    padding-top: 38px;
  }
}
