.portfolio {
  background-image: linear-gradient(to bottom, #f8f8f8, #000000);

  .wrapper {
    padding: 0 21px;
  }
}

@media (min-width: @big-mobile-width) {
  .portfolio {
    .wrapper {
      padding: 0 80px;
    }
  }
}

@media (min-width: @vertical-tablet-width) {
  .portfolio {
    .wrapper {
      padding: 0 20px;
    }
  }
}

@media (min-width: @tablet-width) {
  .portfolio {
    .wrapper {
      padding: 0 42px;
    }
  }
}

@media (min-width: @desktop-width) {
  .portfolio {
    .wrapper {
      padding: 0 20px;
      max-width: 1200px;
      margin: 0 auto;
    }
  }
}

.portfolio__content {
  background-image: linear-gradient(to bottom, #000000, #3c3c3c);
  padding: 30px 10px 48px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

@media (min-width: @vertical-tablet-width) {
  .portfolio__content {
    padding: 30px 20px 90px;
  }
}

@media (min-width: @tablet-width) {
  .portfolio__content {
    padding: 40px 63px 71px;
  }
}

@media (min-width: @desktop-width) {
  .portfolio__content {
    padding: 40px 20px 123px;
  }
}

@media (min-width: @vertical-tablet-width) {
  .portfolio__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
  }
}

@media (min-width: @desktop-width) {
  .portfolio__container {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.portfolio__title {
  color: #f8f8f8;
  text-align: center;
  margin: 0;
  margin-bottom: 30px;
}

@media (min-width: @vertical-tablet-width) {
  .portfolio__title {
    margin-bottom: 70px;
  }
}

@media (min-width: @tablet-width) {
  .portfolio__title {
    margin-bottom: 43px;
  }
}

@media (min-width: @desktop-width) {
  .portfolio__title {
    margin-bottom: 40px;
  }
}

.portfolio__item {
  margin: 0;
  display: flex;
  margin-bottom: 10px;
}

@media (min-width: @vertical-tablet-width) {
  .portfolio__item {
    margin-bottom: 0;
  }
}

.portfolio__item:last-child {
  margin-bottom: 0;
}

.portfolio__image {
  filter: grayscale(100%);
  display: inline-block;
  text-align: center;
  width: 100%;
}

.portfolio__triangle {
  width: 100%;
  height: 0;
  padding-top: 32px;
  clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
  background-color: #000000;
  position: absolute;
  bottom: 0;
  left: 0;
}


@media (min-width: @vertical-tablet-width) {
  .portfolio__triangle {
    padding-top: 50px;
  }
}

@media (min-width: @tablet-width) {
  .portfolio__triangle {
    padding-top: 54px;
  }
}

.portfolio__gradient {
  position: absolute;
  background-image: linear-gradient(to bottom, transparent 0%, #000000);
  bottom: 0;
  left: 0;
  height: 50px;
  width: 100%;
}

@media (min-width: @vertical-tablet-width) {
  .portfolio__gradient {
    padding-top: 70px;
  }
}

@media (min-width: @tablet-width) {
  .portfolio__gradient {
    padding-top: 74px;
  }
}

