.footer {
  background-color: #000000;

  .wrapper {
    padding: 40px 30px 49px 30px;
    color: #f8f8f8;
    text-align: center;
    font-style: normal;
  }
}

@media (min-width: @big-mobile-width) {
  .footer {
    .wrapper {
      padding: 40px 81px 49px;
    }
  }
}

@media (min-width: @vertical-tablet-width) {
  .footer {
    .wrapper {
      padding: 50px 121px 60px 143px;
    }
  }
}

@media (min-width: @tablet-width) {
  .footer {
    .wrapper {
      padding: 40px 42px 55px;
      background-image: url("../img/footer-img.png");
      background-repeat: no-repeat;
      background-position: right 42px top 40px;
      background-size: 434px 478px;
    }
  }
}


@media (min-width: @desktop-width) {
  .footer {
    .wrapper {
      padding: 60px 20px;
      background-size: 500px 550px;
      background-position: right 20px top 60px;
      max-width: 1200px;
      margin: 0 auto;
    }
  }
}

.footer__title {
  margin: 0;
  margin-bottom: 40px;
}

@media (min-width: @vertical-tablet-width) {
  .footer__title {
    margin-bottom: 40px;
  }
}

@media (min-width: @tablet-width) {
  .footer__title {
    margin-bottom: 50px;
  }
}

.footer__contacts {
  font-style: normal;
}

.footer__location {
  font-size: 16px;
}

@media (min-width: @vertical-tablet-width) {
  .footer__location {
    font-size: 30px;
  }
}

.footer__location,
.footer__address,
.footer__floor {
  margin: 0;
  margin-bottom: 16px;
}

.footer__address,
.footer__floor {
  font-family: "BonaNova-Regular", "Arial", sans-serif;
  font-weight: 400;
}

@media (min-width: @desktop-width) {
  .footer__address,
  .footer__floor {
    font-size: 18px;
  }
}

@media (min-width: @tablet-width) {
  .footer__contacts {
    margin-right: 547px;
    text-align: left;
  }
}

.footer__map {
  filter: grayscale(100%);
  width: 100%;
  border-radius: 5px;
  margin-bottom: 16px;
}

@media (min-width: @vertical-tablet-width) {
  .footer__map {
    min-height: 209px;
  }
}

@media (min-width: @tablet-width) {
  .footer__map {
    width: 393px;
  }
}

@media (min-width: @desktop-width) {
  .footer__map {
    width: 478px;
    min-height: 255px;
  }
}

.footer__logo {
  display: block;
  font-size: 16px;
  line-height: 100%;
  color: #f8f8f8;
  margin-bottom: 50px;
}

@media (min-width: @vertical-tablet-width) {
  .footer__logo {
    font-size: 20px;
    margin-bottom: 78px;
  }
}

@media (min-width: @tablet-width) {
  .footer__logo {
    margin-bottom: 33px;
  }
}

@media (min-width: @desktop-width) {
  .footer__logo {
    margin-bottom: 43px;
  }
}

.footer__reservation-button {
  color: #f8f8f8;
  font-size: 16px;
  padding: 10px 30px;
  border: 2px solid #bababa;
  border-radius: 90px;
  display: inline-block;
  line-height: 100%;

  &:hover,
  &:focus {
    background-color: #333333;
  }

  &:active {
    box-shadow: -4px -4px 8px 0 rgba(0, 0, 0, 0.5) inset;
  }
}

@media (min-width: @vertical-tablet-width) {
  .footer__reservation-button {
    min-width: 320px;
    padding: 16px 20px 20px;
    font-size: 20px;
  }
}

@media (min-width: @desktop-width) {
  .footer__reservation-button {
    min-width: 400px;
    padding: 16px 20px 20px;
    font-size: 24px;
  }
}
