.click-and-save {
  background-color: #bababa;
  background-image: url("../img/gray-texture.png");

  .wrapper {
    padding: 30px 20px;
  }
}

@media (min-resolution: 2dppx) {
  .click-and-save {
    background-image: url("../img/gray-texture@2x.png");
  }
}


@media (min-width: @big-mobile-width) {
  .click-and-save {
    .wrapper {
      padding: 30px 80px;
    }
  }
}

@media (min-width: @vertical-tablet-width) {
  .click-and-save {
    .wrapper {
      padding: 50px 80px;
    }
  }
}

@media (min-width: @tablet-width) {
  .click-and-save {
    .wrapper {
      padding: 50px 57px;
    }
  }
}

@media (min-width: @desktop-width) {
  .click-and-save {
    .wrapper {
      padding: 52px 110px 70px;
      max-width: 1200px;
      margin: 0 auto;
    }
  }
}

.click-and-save__box {
  padding: 30px 19px;
  background-color: #000000;
  border-radius: 10px;
  color: #f8f8f8;
  text-align: center;
}

@media (min-width: @vertical-tablet-width) {
  .click-and-save__box {
    padding: 40px 36px 30px;
  }
}

@media (min-width: @tablet-width) {
  .click-and-save__box {
    padding: 40px 127px 45px;
  }
}

@media (min-width: @desktop-width) {
  .click-and-save__box {
    padding: 48px 70px;
  }
}

.click-and-save__title {
  margin: 0;
  margin-bottom: 20px;
}

@media (min-width: @vertical-tablet-width) {
  .click-and-save__title {
    margin-bottom: 15px;
  }
}

@media (min-width: @tablet-width) {
  .click-and-save__title {
    margin-bottom: 36px;
  }
}

@media (min-width: @desktop-width) {
  .click-and-save__title {
    margin-bottom: 30px;
  }
}

.click-and-save__offer {
  margin: 0;
  margin-bottom: 40px;
  color: #bababa;
  font-size: 16px;
}

@media (min-width: @vertical-tablet-width) {
  .click-and-save__offer {
    margin-bottom: 41px;
    font-size: 24px;
  }
}

@media (min-width: @tablet-width) {
  .click-and-save__offer {
    margin-bottom: 36px;
  }
}

@media (min-width: @desktop-width) {
  .click-and-save__offer {
    margin-bottom: 30px;
    font-size: 30px;
  }
}

.click-and-save__reservation-button {
  color: #f8f8f8;
  min-width: 254px;
  padding: 10px 18px;
  border: 2px solid #bababa;
  border-radius: 90px;
  display: inline-block;
  line-height: 100%;
  font-size: 16px;

  &:hover,
  &:focus {
    background-color: #333333;
  }

  &:active {
    box-shadow: -4px -4px 8px 0 rgba(0, 0, 0, 0.5) inset;
  }
}

@media (max-width: @small-mobile-width-only) {
  .click-and-save__reservation-button {
    min-width: 204px;
  }
}

@media (min-width: @vertical-tablet-width) {
  .click-and-save__reservation-button {
    min-width: 320px;
    padding: 16px 20px 20px;
    font-size: 20px;
  }
}

@media (min-width: @desktop-width) {
  .click-and-save__reservation-button {
    min-width: 400px;
    padding: 16px 20px;
    font-size: 24px;
  }
}
