.offer {
  background-color: #000000;
  background-image: url("../img/gray-texture.png");
  position: relative;

  .wrapper {
    color: #f8f8f8;
    padding: 50px 20px 80px;
  }
}

@media (min-resolution: 2dppx) {
  .offer {
    background-image: url("../img/gray-texture@2x.png");
  }
}

@media (min-width: @big-mobile-width) {
  .offer {
    .wrapper {
      padding: 50px 81px 80px;
    }
  }
}

@media (min-width: @vertical-tablet-width) {
  .offer {
    .wrapper {
      padding: 60px 80px 90px;
    }
  }
}

@media (min-width: @tablet-width) {
  .offer {
    .wrapper {
      padding: 40px 52px 140px;
    }
  }
}

@media (min-width: @desktop-width) {
  .offer {
    .wrapper {
      padding: 60px 60px 130px;
      max-width: 1200px;
      margin: 0 auto;
    }
  }
}

.offer__title {
  text-align: center;
  margin: 0 50px 37px 0;
}

@media (min-width: @vertical-tablet-width) {
  .offer__title {
    margin: 0 78px 47px 0;
  }
}

@media (min-width: @tablet-width) {
  .offer__title {
    margin: 0 64px 74px 0;
  }
}

@media (min-width: @desktop-width) {
  .offer__title {
    font-size: 54px;
    margin: 0 43px 32px 0;
  }
}

.offer__title--zone {
  text-transform: lowercase;
  display: block;
  margin-top: -17px;
  margin-left: 128px;
}

@media (min-width: @vertical-tablet-width) {
  .offer__title--zone {
    font-size: 16px;
    margin-top: -28px;
    margin-left: 192px;
  }
}

@media (min-width: @desktop-width) {
  .offer__title--zone {
    font-size: 20px;
    margin-top: -33px;
    margin-left: 280px;
  }
}

.offer__item {
  display: flex;
  width: 100%;
}

.offer__item:first-of-type {
  margin-bottom: 30px;
}

@media (min-width: @tablet-width) {
  .offer__item:first-of-type {
    margin-bottom: 0;
  }
}

.offer__item-title {
  font-size: 16px;
  line-height: 100%;
  font-weight: 300;
  margin: 0;
  margin-bottom: 20px;
  text-align: center;
}

.offer__item-link {
  color: #333333;
  line-height: 100%;
  text-align: center;

  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

@media (max-width: @small-mobile-width-only) {
  .offer__item-title,
  .offer__item-link {
    word-break: break-word;
    font-size: 12px;
  }
}

@media (min-width: @vertical-tablet-width) {
  .offer__item-title,
  .offer__item-link {
    font-size: 20px;
    text-align: left;
  }
}

@media (min-width: @desktop-width) {
  .offer__item-link {
    line-height: 200%;
  }
}

.offer__box {
  display: flex;
  flex-direction: column;
  background-color: #bababa;
  color: #181818;
  border-radius: 0 10px 10px 0;
  padding: 40px 9px;
  width: 100%;
  margin-left: -2px;
}

@media (min-width: @vertical-tablet-width) {
  .offer__box {
    padding: 60px 30px;
  }
}

@media (min-width: @tablet-width) {
  .offer__box {
    padding: 55px 20px;
  }
}

@media (min-width: @desktop-width) {
  .offer__box {
    padding: 66px 27px;
    margin-left: 0;
  }

  .offer__box--electro {
    padding: 66px 37px;
  }
}

.offer__picture {
  display: flex;
}

.offer__img {
  border-radius: 10px 0  0 10px;
  min-width: 203px;
  display: block;
  object-fit: cover;
}

@media (min-width: @vertical-tablet-width) {
  .offer__img {
    min-width: 300px;
  }
}

@media (min-width: @tablet-width) {
  .offer__img {
    min-width: 250px;
    min-height: 200px;
  }
}

@media (min-width: @desktop-width) {
  .offer__img {
    min-width: 300px;
  }
}

@media (min-width: @tablet-width) {
  .offer__container {
    display: flex;
    gap: 93px;
  }
}

.offer__triangle {
  width: 50%;
  height: 0;
  padding-top: 14px;
  clip-path: polygon(100% 0%, 0% 100%, 100% 100%);
  background-color: #f8f8f8;
  position: absolute;
  bottom: 0;
  right: 0;
}

@media (min-width: @vertical-tablet-width) {
  .offer__triangle {
    padding-top: 22px;
  }
}

@media (min-width: @tablet-width) {
  .offer__triangle {
    padding-top: 32px;
  }
}

@media (min-width: @desktop-width) {
  .offer__triangle {
    padding-top: 38px;
  }
}

