.header {
  position: fixed;
  background-color: #181818;
  min-width: 100%;
  z-index: 1;

  .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 21px 20px;
    color: #f8f8f8;
    font-size: 20px;
    position: relative;
  }
}

@media (min-width: @big-mobile-width) {
  .header {
    .wrapper {
      padding: 21px 80px;
    }
  }
}

@media (min-width: @vertical-tablet-width) {
  .header {
    .wrapper {
      padding: 20px 30px;
    }
  }
}

@media (min-width: @tablet-width) {
  .header {
    .wrapper {
      padding: 20px 44px;
    }
  }
}

@media (min-width: @desktop-width) {
  .header {
    .wrapper {
      padding: 20px;
      max-width: 1200px;
      margin: 0 auto;
    }
  }
}

.header__logo {
  color: #f8f8f8;
  border: 1px solid #f8f8f8;
  border-radius: 90px;
  padding: 9px 30px;
  line-height: 100%;

  &:hover,
  &:focus {
    background-color: #f8f8f8;
    color: #000000;
  }
}


@media (min-width: @vertical-tablet-width) {
  .header__logo {
    padding: 9px 32px;
  }
}

@media (min-width: @desktop-width) {
  .header__logo {
    padding: 11px 33px;
    border: 2px solid #f8f8f8;
    font-size: 24px;
  }
}
