html {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  height: 100%;
  min-width: 320px;
  font-family: "Uto-light", "Arial", sans-serif;
  font-weight: 300;
  font-size: 14px;
}

@media (min-width: @vertical-tablet-width) {
  body {
    font-size: 16px;
  }
}

@media (min-width: @desktop-width) {
  body {
    font-size: 18px;
  }
}

h1,
h2 {
  font-size: 24px;
  font-weight: 300;
  text-transform: uppercase;
}

@media (min-width: @vertical-tablet-width) {
  h1,
  h2 {
    font-size: 40px;
  }
}

@media (min-width: @desktop-width) {
  h1 {
    font-size: 54px;
  }

  h2 {
    font-size: 50px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
  font-family: "Uto-light", "Arial", sans-serif;
  font-weight: 300;
}
