.faq {
  background-color: #f8f8f8;

  .wrapper {
    padding: 30px 21px;
  }
}

@media (min-width: @big-mobile-width) {
  .faq {
    .wrapper {
      padding: 30px 81px;
    }
  }
}

@media (min-width: @vertical-tablet-width) {
  .faq {
    .wrapper {
      padding: 50px 20px 57px;
    }
  }
}

@media (min-width: @tablet-width) {
  .faq {
    .wrapper {
      padding: 60px 60px 65px 42px;
    }
  }
}

@media (min-width: @desktop-width) {
  .faq {
    .wrapper {
      padding: 60px 20px;
      max-width: 1200px;
      margin: 0 auto;
    }
  }
}

.faq__title {
  color: #181818;
  margin: 0;
  margin-bottom: 15px;
  text-align: center;
}

@media (min-width: @vertical-tablet-width) {
  .faq__title {
    margin-bottom: 30px;
    font-size: 30px;
  }
}

@media (min-width: @tablet-width) {
  .faq__title {
    margin-bottom: 20px;
    text-align: left;
  }
}

@media (min-width: @desktop-width) {
  .faq__title {
    margin-bottom: 60px;
    font-size: 50px;
  }
}

.faq__question {
  margin: 0;
  padding: 8px 17px;
  line-height: 150%;
  cursor: pointer;
  user-select: none;
}

@media (min-width: @vertical-tablet-width) {
  .faq__question {
    padding: 9px 50px 14px 14px;
    line-height: 200%;
  }
}

@media (min-width: @tablet-width) {
  .faq__question {
    padding: 10px 65px 7px 24px;
    font-size: 20px;
  }
}

@media (min-width: @desktop-width) {
  .faq__question {
    padding: 15px 100px 10px 30px;
  }
}

.faq__list {
  margin: 0;
}

.faq__answer {
  margin: 0;
  display: none;
  font-family: "BonaNova-Regular", "Arial", sans-serif;
  font-weight: 400;
  padding: 0 17px 10px;
}

@media (min-width: @vertical-tablet-width) {
  .faq__answer {
    padding-right: 50px;
    padding-bottom: 14px;
  }
}

@media (min-width: @tablet-width) {
  .faq__answer {
    font-size: 20px;
    padding: 0 65px 15px 24px;
  }
}

@media (min-width: @desktop-width) {
  .faq__answer {
    padding: 0 100px 30px 30px;
  }
}

.faq__item {
  color: #181818;
  margin-bottom: 11px;
  border: 2px solid #bababa;
  border-radius: 10px;
}

@media (min-width: @vertical-tablet-width) {
  .faq__item {
    background-image: url("../img/open-faq.svg");
    background-repeat: no-repeat;
    background-position: top 12px right 12px;
    margin-bottom: 20px;
  }
}

@media (min-width: @tablet-width) {
  .faq__item {
    background-position: top 15px right 24px;
  }
}

@media (min-width: @desktop-width) {
  .faq__item {
    background-position: top 9px right 30px;
    background-size: 40px 40px;
  }
}

.faq__item:last-child {
  margin-bottom: 0;
}

.faq__item.active .faq__answer {
  display: block;
}
