.hero {
  background-color: #f8f8f8;

  .wrapper {
    padding: 162px 28px 452px;
    background-image: url("../img/hand.png");
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: 302px 450px;
    font-size: 16px;
  }
}


@media (min-resolution: 2dppx) {
  .hero {
    .wrapper {
      background-image: url("../img/hand@2x.png");
    }
  }
}

@media (min-width: @big-mobile-width) {
  .hero {
    .wrapper {
      padding: 162px 89px 452px;
      background-size: 265px 441px;
    }
  }
}

@media (min-width: @vertical-tablet-width) {
  .hero {
    .wrapper {
      padding: 160px 73px 583px 86px;
      font-size: 20px;
      background-size: 368px 545px;
    }
  }
}

@media (min-width: @tablet-width) {
  .hero {
    .wrapper {
      padding: 184px 420px 98px 42px;
      background-position: right 42px bottom;
      background-size: 320px 486px;
    }
  }
}

@media (min-width: @desktop-width) {
  .hero {
    .wrapper {
      padding: 238px 460px 148px 46px;
      font-size: 24px;
      background-size: 460px 700px;
      max-width: 1200px;
      margin: 0 auto;
    }
  }
}

.hero__title {
  margin: 0;
  margin-bottom: 10px;
  line-height: 130%;
}

@media (min-width: @vertical-tablet-width) {
  .hero__title {
    margin-bottom: 15px;
  }
}

@media (min-width: @desktop-width) {
  .hero__title {
    margin-bottom: 20px;
  }
}

.hero__text {
  margin: 0;
  margin-bottom: 40px;
  line-height: 100%;
}

@media (min-width: @vertical-tablet-width) {
  .hero__text {
    margin-bottom: 92px;
  }
}

@media (min-width: @tablet-width) {
  .hero__text {
    margin-bottom: 102px;
  }
}

@media (min-width: @desktop-width) {
  .hero__text {
    margin-bottom: 160px;
  }
}

.hero__reservation-button {
  text-align: center;
  color: #f8f8f8;
  line-height: 100%;
  padding: 12px 40px;
  background-color: #181818;
  border-radius: 90px;
  margin: 0 auto;
  display: block;
  width: 264px;
  word-break: break-word;

  &:hover,
  &:focus {
    background-color: #333333;
  }

  &:active {
    box-shadow: -4px -4px 8px 0 rgba(0, 0, 0, 0.5) inset;
  }
}

@media (min-width: @vertical-tablet-width) {
  .hero__reservation-button {
    width: 320px;
    padding: 18px 40px 22px;
  }
}

@media (min-width: @tablet-width) {
  .hero__reservation-button {
    margin: 0;
  }
}

@media (min-width: @desktop-width) {
  .hero__reservation-button {
    width: 360px;
    padding: 18px 40px;
  }
}
